.App {
  text-align: center;
}

.loader {
  margin: auto;
  position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.loading-text {
  font-weight: bold;
  font-size: 24px;
  padding-top: 20px;
}
.current-weather-container {
    padding-bottom: 30px;
    border-bottom: 1px dashed black;
    margin-bottom: 15px;
}

.current-weather {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.current-weather-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

p {
    font-size: 36px;
    font-weight: bold;
    margin: 0;
    padding-left: 20px;
}

.city-name {
    font-weight: bolder;
}
.ct-series-b .ct-line,
.ct-series-b .ct-point {
    stroke: blue;
}

.ct-chart .ct-label.ct-horizontal {
    white-space: nowrap;
}

h4 {
    padding-top: 5px;
    padding-bottom: 20px;
    font-weight: bold;
}
body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

i {
  font-size: 95px;
}
